.benchmarks {
    padding-top: var(--navHeight);
}

.benchmarksContainer {
    background-color: var(--themeColorAccentNeg10);
    padding: 1rem 0;
}

.benchmarksDownload {
    background-color: var(--themeColorPrimary);
    width: fit-content;
    border-radius: 4px;
    padding: .8rem .4rem;
    font-size: .7rem;
    margin: 1rem auto;
}

.benchmarksDownload:hover {
    cursor: pointer;
    transform: scale(1.01);
    opacity: .9;
}

.benchmarksPDFViewer {
    display: flex;
    justify-content: center;
    width: 100%;
}


.benchmarksPDFViewer a {
    display: flex;
}

.benchmarksPDFViewer a:hover {
    color: var(--themeColorPrimary);
    text-decoration: underline;
}

.benchmarksPDFViewer svg {
    font-size: .6rem;
    align-self: center;
    margin-right: .2rem;
}

.paginationViewer {
    display: flex;
    flexDirection: row;
    position: absolute;
}

.PDFViewer {
    position: relative;
}

.paginationButton {
    display: block;
    border: 1.5px solid white;
    padding: .2rem .6rem;
    border-radius: 5px;
    transition:
        background-color .3s,
        border-color .3s;
}

.paginationButton:hover {
    cursor: pointer;
    background-color: var(--themeColorEdammoBlue);
    border-color: var(--themeColorEdammoBlue);
    color: white;
}