.benchmarks {
}

/*
BANNER
*/

.benchmarksBanner {
  padding: 1rem;
  padding-top: calc(var(--navHeight) + 1rem);
  background-color: var(--themeColorAccentNeg10);
}

.benchmarksBanner a {
  display: flex;
}

.benchmarksBanner a:hover {
  color: var(--themeColorPrimary);
  text-decoration: underline;
}

.benchmarksBanner svg {
  font-size: 0.6rem;
  align-self: center;
  margin-right: 0.2rem;
}

/*
ERR
*/

.benchmarksErr_header {
  padding: 1rem;
}

.benchmarksErr_header > h2 {
  font-size: 1.4rem;
  max-width: 40ch;
  margin-bottom: 0.6rem;
  font-weight: bold;
}

.benchmarksErr_header > a {
  display: flex;
  margin-bottom: 1rem;
}

.benchmarksErr_header > a:hover {
  color: var(--themeColorPrimary);
  text-decoration: underline;
}

.benchmarksErr_header svg {
  font-size: 0.7rem;
  margin-right: 0.2rem;
}

.benchmarksErr_header > p {
  font-size: 0.7rem;
  max-width: 70ch;
  margin-bottom: 0.2rem;
}

.benchmarksErr_legendItem {
  display: flex;
  align-items: center;
}

.benchmarksErr_edammo {
  width: 1rem;
  height: 1rem;
  background-color: rgba(99, 136, 180, 1);
  margin-right: 1ch;
}

.benchmarksErr_google {
  width: 1rem;
  height: 1rem;
  background-color: rgba(226, 124, 113, 1);
  margin-right: 1ch;
}

.benchmarksErr_graphHeader {
  font-size: 0.6rem;
  margin-bottom: 1rem;
}

.benchmarksErr_graphs {
  padding: 0 1rem;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.benchmarksErr_graphs > * {
  width: 100%;
  margin: 0.5rem;
}

.benchmarksErr_graphs img {
  width: 100%;
}

/*
ACC
*/
.benchmarksAcc {
  padding: 1rem;
}

.benchmarksAcc > h2 {
  font-weight: bold;
}

.benchmarksAcc_header {
  margin-bottom: 0.4rem;
}

.benchmarksAcc_top {
  display: flex;
  justify-content: space-around;
}

.benchmarksAcc_top > * {
  width: 100%;
  margin: 1rem;
}

.benchmarksAcc_top img {
  width: 100%;
}

.benchmarksAcc_bot {
  display: flex;
  padding: 0 2rem;
}

.benchmarksAcc_bot > * {
  width: 100%;
}

.benchmarksAcc_botItem > p {
  width: fit-content;
  padding: 1rem;
}

.benchmarksAcc_botItem > img {
  width: 100%;
}

/*
VARIANCE
*/

.benchmarksVariance {
  padding: 1rem;
}

.benchmarksVariance > h2 {
  text-align: center;
  font-weight: bold;
  margin-bottom: 1rem;
}

.benchmarksVariance_graphs {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.benchmarksVariance_graphs > * {
  width: 100%;
  margin: 1rem;
}

.benchmarksVariance_img {
  width: 100%;
}

/*
SCATTER
*/

.benchmarkScatter {
  display: flex;
}

.benchmarkScatter > * {
  width: 100%;
  margin: 1rem;
}

.benchmarkScatter_left > img {
  width: 100%;
}

.benchmarkScatter_right {
  padding-top: 2rem;
}

.benchmarkScatter_right > h2 {
  font-size: 1.4rem;
  margin-bottom: 2rem;
  font-weight: bold;
}

.benchmarkScatter_right > p {
  font-size: 0.7rem;
  max-width: 25ch;
  margin: auto;
}
