/*
A bit of this is improperly scoped. But it works.
*/

.ourblog {
    padding: 1rem;
    padding-top: var(--navHeight);
    text-align: center;
    background-image: url('./res/bannerbg.jpg');
    background-size: cover;
}

.ourblog :is(ul, ol) {
    list-style-position: inside;
}

.ourblogPost {
    background-color: #eee;
    color: var(--themeColorDark);
    max-width: 800px;
    margin: 1rem auto;
    padding: 1rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    font-size: 70%;
}

.ourblogPost_title {
    font-family: 'Noto Serif', serif;
}

.ourblogPost_date {
    font-size: 70%;
    font-weight: bolder;
    margin-bottom: 1rem;
    text-transform: uppercase;
}

.ourblogPost img {
    display: block;
    max-width: 80%;
    margin: 1rem auto;
}