@media only screen and (max-width: 800px) {

/*
BANNER
*/

.rndBanner > h1 {
    width: fit-content;
    margin: auto;
    text-align: center;
}

/*
MID
*/

.rndMid_col {
    margin-bottom: 1rem;
}

.rndMid_papers {
    flex-direction: column;
}

.rndMid_gist > img {
    margin: auto;
    display: block;
    margin-bottom: 1.2rem;
}

.rndMid_gist > h3 {
    text-align: center;
    margin: .5rem auto;
}

.rndMid_gist > p {
    max-width: 60ch;
    margin: .5rem auto;
    text-align: center;
    margin-bottom: 1rem;
}

/*
BOTTOM
*/

.rndBottom {
    flex-direction: column;
}

.rndBottom > * {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.rndBottom_text {
    margin: auto;
    margin-bottom: 1rem;
}

.rndBottom_text > h3 {
    margin: .5rem auto;
    text-align: center;
}

.rndBottom_text > p {
    text-align: center;
}

}