.stocks {
    padding: var(--navHeight) 0;
}

/*
HEADING
*/
.stocksHeading {
    background-color: var(--themeColorAccentNeg10);
    padding: .5rem;
    margin-bottom: 1rem;
}

.stocksHeading > h1 {
    text-align: center;
    margin: 0 auto;
}

.stocksHeading > hr {
    max-width: 50ch;
    margin: .5rem auto;
    font-size: .8rem;
}

.stocksHeading > p {
    text-align: center;
    max-width: 50ch;
    margin: auto;
    font-size: .8rem;
}

/*
GRAPHS
*/

.stocksGraphs_legend {
    display: flex;
    align-items: center;
    margin-bottom: .5rem;
}

.stocksGraphs_blue {
    width: 1rem;
    height: 1rem;
    background-color: blue;
    margin-right: .5ch;
}

.stocksGraphs_legendSpace {
    width: 1rem;
    height: 1rem;
}

.stocksGraphs_red {
    width: 1rem;
    height: 1rem;
    background-color: red;
    margin-right: .5ch;
}

.stocksGraphs {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.stocksGraphs > * {
    width: 70%;
    min-width: 400px;
    justify-content: center;
}