@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yantramanav:wght@100;300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:ital,wght@0,400;0,700;1,400;1,700&display=swap);
/* font-family: 'Yantramanav', sans-serif; */
/* font-family: 'Noto Serif', serif; */

*, *::before, *::after {
  margin: 0;
  box-sizing: border-box;
}

:root {
  /*--themeColorPrimary: #E84A43;*/
  --themeColorPrimary: rgba(0, 113, 239, 1);
  --themeColorAccent: rgb(185, 192, 213);
  --themeColorAccentNeg10: rgb(95 104 131);
  --themeColorDark: rgba(50, 50, 50);
  --themeColorDarkNeg10: rgba(75, 75, 75);
  --themeColorEdammoBlue: rgba(0, 113, 239, 1);

  font-family: 'Poppins', sans-serif;
  font-size: calc(1vw + 16px);
  color: white;
  background-color: rgba(50, 50, 50);
  background-color: var(--themeColorDark);

  --navHeight: 3rem;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

a {
  text-decoration: none;
  color: inherit;
}
.nav {
  width: 100%;
  font-size: 0.5rem;
  padding: 0 0.2rem;
  align-items: center;
  height: var(--navHeight);
  position: fixed;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  z-index: 100;
  opacity: 1;
  transition: background-color 0.2s, opacity 0.4s;
}

.nav_logo {
  position: absolute;
  height: var(--navHeight);
  display: flex;
  align-items: center;
}

.nav_logoText {
  font-weight: light;
  font-size: 1rem;
}

.nav_logo > img {
  margin-right: 1.5ch;
}

.nav_cioSticker {
  margin-left: 0.5rem;
  display: none;
}

.nav_cioSticker > img {
  width: 8rem;
}

.nav--opaque {
  background-color: var(--themeColorDark);
  box-shadow: 0 0 8px 0 rgba(35, 35, 35, 1);
}

.nav--hidden {
  opacity: 0;
  pointer-events: none;
}

.nav_links {
  display: flex;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: absolute;
  height: var(--navHeight);
  right: 0;
}

.nav_nonlink,
.nav a {
  display: flex;
  align-items: center;
  color: white;
  margin-right: 1rem;
  transition: color 0.3s;
  padding: 0.5rem 0rem;
  height: 100%;
}

a.nav_link--active {
  color: var(--themeColorEdammoBlue);
  text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
}

.nav a:hover {
  color: var(--themeColorEdammoBlue);
}

.nav_contactbutton {
  border: 1.5px solid white;
  padding: 0.2rem 0.6rem;
  border-radius: 5px;
  transition: background-color 0.3s, border-color 0.3s;
}

.nav_contactbutton:hover {
  cursor: pointer;
  background-color: var(--themeColorEdammoBlue);
  border-color: var(--themeColorEdammoBlue);
  color: white;
}

@media only screen and (max-width: 1210px) {


.nav_cioSticker {
    display: flex;
}

.nav_nonlink,
.nav a {
    font-size: .7rem;
    padding: .2rem 0;
    border-bottom: 1px solid var(--themeColorDarkNeg10);
}

}
.navDropdown_sub {
    display: none;
    position: absolute;
    flex-direction: column;
    text-align: right;
    box-shadow: 0 0 .5rem 0 rgba(0, 0, 0, .5);
    background-color: var(--themeColorDark);
    padding: .4rem;
    border-radius: 2px;
    margin-top: 0rem;
    -webkit-transform: translateX(-1ch);
            transform: translateX(-1ch);
}

.navDropdown_sub > a {
    margin: 0;
    padding: .1rem 0;
}

.navDropdown:hover > .navDropdown_sub {
    display: flex;
}
@media only screen and (max-width: 1210px) {

.navDropdown_sub {
    -webkit-transform: translateX(-55%);
            transform: translateX(-55%);
    text-align: left;
}

}
/*
ANIMATIONS
*/

@-webkit-keyframes fadeFromLeft {
  0% {
    margin-left: -1rem;
    opacity: 0;
  }
  20% {
    margin-left: -1rem;
    opacity: 0;
  }
  100% {
    margin-left: 0;
    opacity: 1;
  }
}

@keyframes fadeFromLeft {
  0% {
    margin-left: -1rem;
    opacity: 0;
  }
  20% {
    margin-left: -1rem;
    opacity: 0;
  }
  100% {
    margin-left: 0;
    opacity: 1;
  }
}

/*
SPLASH
*/

.homeSplash {
  min-height: 100vh;
  padding: 3.5rem 2rem;
  background-image: url(/static/media/bannerbg.7ca65681.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.homeSplash_news {
  color: var(--themeColorPrimary);
  font-size: 0.9rem;
}

.homeSplash_news:hover {
  text-decoration: underline;
  opacity: 0.9;
}

.homeSplash > h1 {
  font-size: 2rem;
  margin-bottom: 1.5rem;
  -webkit-animation-name: fadeFromLeft;
          animation-name: fadeFromLeft;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  text-shadow: 1px 1px 2px black, 0 0 25px gray, 0 0 5px darkgray;
}

.homeSplash > p {
  max-width: 30ch;
  margin-bottom: 1rem;
  margin-bottom: 1.2rem;
  -webkit-animation-name: fadeFromLeft;
          animation-name: fadeFromLeft;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  text-shadow: 1px 1px 2px black, 0 0 25px gray, 0 0 5px darkgray;
}

.homeSplash_learnMore {
  color: var(--themeColorDark);
  background-color: var(--themeColorPrimary);
  border: 1.5px solid var(--themeColorPrimary);
  padding: 0.4rem 0.8rem;
  border-radius: 5px;
  font-size: 0.5rem;
  transition: background-color 0.3s, border-color 0.3s;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 1.5rem;
  -webkit-animation-name: fadeFromLeft;
          animation-name: fadeFromLeft;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}

.homeSplash_learnMore:hover {
  color: white;
  border-color: white;
  background-color: var(--themeColorDark);
}

.homeSplash .globeAnimation {
  display: none;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: absolute;
  top: 30%;
  right: 2rem;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

@media only screen and (max-width: 1100px) {
  .homeSplash .globeAnimation {
    display: none;
  }
}

