@media only screen and (max-width: 1000px) {

.aboutUsLeadership_cards {
    flex-direction: column;
    max-width: 380px;
    margin: auto;
}

.aboutUsAdvisory_members {
    flex-direction: column;
}

.aboutUsAdvisory_member {
    border-bottom: 1px solid var(--themeColorDark);
}

.aboutUsNews_articles {
    flex-direction: column;
    align-items: center;
}

.aboutUsNews_article {
    margin-bottom: 1rem;
}

.aboutUsHiring {
    margin-bottom: 1rem;
    padding: 1rem 0;
}

.aboutUsHiring_text p {
    font-size: 1rem;
}

}