@media only screen and (max-width: 800px) {

/*
MOST
*/

.homeMost_cards {
    flex-direction: column;
}

.homeMost_card {
    border-bottom: none;
}

.homeMost_cardHr {
    display: none;
}

/*
ADVANTAGE
*/

.homeAdvantage > h2 {
    text-align: center;
}

.homeAdvantage_statcards {
    flex-direction: column;
}

.homeAdvantage_statcard {
    margin-bottom: 1rem;
}

.homeAdvantage_table {
    width: 305px;
    margin-right: auto;
    margin-left: auto;
}

.homeAdvantage_table > * {
    width: 100%;
}

/*
TABLE
*/

.homeAdvantage_tableCol1 .homeAdvantage_tableHeader {
    padding-right: 1rem;
}

.homeAdvantage_tableCol,
.homeAdvantage_tableHeader {
    width: 20%;
}

/*
LET
*/
.homeLet {
    flex-direction: column;
}

.homeLet_left {
    width: 100%;
}

.homeLet_right {
    width: 100%;
}

/*
CONTACT
*/

.homeContact {
    background-color: black;
    padding: 2rem 0;
    height: fit-content;
}

.homeContact_nonBg {
    flex-direction: column;
    position: relative;
}

.homeContact_nonBg > h3 {
    margin-bottom: 1rem;
    text-align: center;
}

.homeContact_particles {
    display: none;
}

.homeContact_nonBg > form > input {
    height: 1.4rem;
}

}