.nav {
  width: 100%;
  font-size: 0.5rem;
  padding: 0 0.2rem;
  align-items: center;
  height: var(--navHeight);
  position: fixed;
  transform: translateZ(0);
  z-index: 100;
  opacity: 1;
  transition: background-color 0.2s, opacity 0.4s;
}

.nav_logo {
  position: absolute;
  height: var(--navHeight);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
}

.nav_logoText {
  font-weight: light;
  font-size: 1rem;
}

.nav_logo > img {
  margin-right: 1.5ch;
}

.nav_cioSticker {
  margin-left: 0.5rem;
  display: none;
}

.nav_cioSticker > img {
  width: 8rem;
}

.nav--opaque {
  background-color: var(--themeColorDark);
  box-shadow: 0 0 8px 0 rgba(35, 35, 35, 1);
}

.nav--hidden {
  opacity: 0;
  pointer-events: none;
}

.nav_links {
  display: flex;
  align-items: center;
  width: fit-content;
  position: absolute;
  height: var(--navHeight);
  right: 0;
}

.nav_nonlink,
.nav a {
  display: flex;
  align-items: center;
  color: white;
  margin-right: 1rem;
  transition: color 0.3s;
  padding: 0.5rem 0rem;
  height: 100%;
}

a.nav_link--active {
  color: var(--themeColorEdammoBlue);
  text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
}

.nav a:hover {
  color: var(--themeColorEdammoBlue);
}

.nav_contactbutton {
  border: 1.5px solid white;
  padding: 0.2rem 0.6rem;
  border-radius: 5px;
  transition: background-color 0.3s, border-color 0.3s;
}

.nav_contactbutton:hover {
  cursor: pointer;
  background-color: var(--themeColorEdammoBlue);
  border-color: var(--themeColorEdammoBlue);
  color: white;
}
