@media only screen and (max-width: 800px) {

.benchmarksErr_graphs {
    flex-direction: column;
}

.benchmarksAcc_top {
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.benchmarksAcc_bot {
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.benchmarkScatter {
    align-items: center;
    justify-content: center;
    padding: .5rem;
    flex-direction: column-reverse;
}

.benchmarkScatter_right > h2 {
    text-align: center;
    padding: 0 2rem;
}

}