/*
ANIMATIONS
*/

@keyframes fadeFromLeft {
  0% {
    margin-left: -1rem;
    opacity: 0;
  }
  20% {
    margin-left: -1rem;
    opacity: 0;
  }
  100% {
    margin-left: 0;
    opacity: 1;
  }
}

/*
SPLASH
*/

.homeSplash {
  min-height: 100vh;
  padding: 3.5rem 2rem;
  background-image: url('./res/bannerbg.jpg');
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.homeSplash_news {
  color: var(--themeColorPrimary);
  font-size: 0.9rem;
}

.homeSplash_news:hover {
  text-decoration: underline;
  opacity: 0.9;
}

.homeSplash > h1 {
  font-size: 2rem;
  margin-bottom: 1.5rem;
  animation-name: fadeFromLeft;
  animation-duration: 2s;
  text-shadow: 1px 1px 2px black, 0 0 25px gray, 0 0 5px darkgray;
}

.homeSplash > p {
  max-width: 30ch;
  margin-bottom: 1rem;
  margin-bottom: 1.2rem;
  animation-name: fadeFromLeft;
  animation-duration: 3s;
  text-shadow: 1px 1px 2px black, 0 0 25px gray, 0 0 5px darkgray;
}

.homeSplash_learnMore {
  color: var(--themeColorDark);
  background-color: var(--themeColorPrimary);
  border: 1.5px solid var(--themeColorPrimary);
  padding: 0.4rem 0.8rem;
  border-radius: 5px;
  font-size: 0.5rem;
  transition: background-color 0.3s, border-color 0.3s;
  width: fit-content;
  margin-bottom: 1.5rem;
  animation-name: fadeFromLeft;
  animation-duration: 4s;
}

.homeSplash_learnMore:hover {
  color: white;
  border-color: white;
  background-color: var(--themeColorDark);
}

.homeSplash .globeAnimation {
  display: none;
  width: fit-content;
  position: absolute;
  top: 30%;
  right: 2rem;
  transform: translateY(-50%);
}

@media only screen and (max-width: 1100px) {
  .homeSplash .globeAnimation {
    display: none;
  }
}

/*
MOST
*/

.homeMost {
  padding: 2rem 4rem;
  background-image: url('./res/mostbg.jpg');
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.homeMost > h2 {
  text-align: center;
  max-width: 30ch;
  margin: auto;
  margin-bottom: 1rem;
  font-size: 1rem;
}

.homeMost_cards {
  display: flex;
  justify-content: space-around;
}

.homeMost_cards > * {
  width: 100%;
}

.homeMost_card {
  width: 60%;
  margin: auto;
  border-radius: 1px;
  min-height: 9rem;
}

.homeMost_card > h3 {
  font-size: 2rem;
  text-align: center;
  color: var(--themeColorPrimary);
  margin-bottom: 1rem;
}

.homeMost_card > p {
  text-align: center;
  font-size: 0.6rem;
}

.homeMost_cardHr {
  width: 55%;
  margin: auto;
  height: 3px;
  background-color: gray;
  border-radius: 1px;
}

/*
ADVANTAGE
*/
.homeAdvantage {
  padding: 2rem;
}

.homeAdvantage > * {
  max-width: 1000px;
}

.homeAdvantage > h2 {
  text-decoration: underline;
  width: fit-content;
  margin: auto;
  margin-bottom: 1.5rem;
  color: var(--themeColorPrimary);
  color: white;
}

.homeAdvantage_statcards {
  display: flex;
  margin: auto;
  /*border-bottom: 4px solid white;*/
  padding-bottom: 2rem;
  margin-bottom: 2rem;
}

.homeAdvantage_statcards > * {
  width: 100%;
}

.homeAdvantage_statcard {
  width: 60%;
  margin: auto;
  text-align: center;
}

.homeAdvantage_statcard > h3 {
  font-size: 1.8rem;
  color: var(--themeColorPrimary);
}

.homeAdvantage_statcardHr {
  background-color: gray;
  height: 3px;
  margin-bottom: 1rem;
}

.homeAdvantage_statcard > p {
  font-size: 0.6rem;
}

/*
ADVANTAGE_TABLE
*/
.homeAdvantage_table {
  display: flex;
  align-items: flex-end;
  justify-content: right;
  width: fit-content;
  max-width: 1000px;
  margin-left: auto;
  margin-right: 2rem;
  margin-bottom: 2rem;
  position: relative;
}

.homeAdvantage_table .globeAnimation {
  position: absolute;
  left: -15rem;
  top: 50%;
  transform: translateY(-50%);
  clear: both;
}

@media only screen and (max-width: 1100px) {
  .homeAdvantage_table .globeAnimation {
    display: none;
  }
}

.homeAdvantage_tableCol {
  margin-left: 0.5rem;
}

.homeAdvantage_tableCol1 {
  margin-right: 1rem;
}

.homeAdvantage_tableHeader {
  background-color: var(--themeColorAccentNeg10);
  color: black;
  border-top: none;
  font-weight: bold;
}

.homeAdvantage_table p {
  height: 2rem;
  font-size: 0.4rem;
  min-width: 5rem;
  width: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.homeAdvantage_table p:not(.homeAdvantage_tableHeader) {
  background-color: rgba(60, 60, 60);
  border-top: 2px solid var(--themeColorDarkNeg10);
  width: 4rem;
}

.homeAdvantage_table p > svg {
  width: 100%;
  font-size: 1.5rem;
}

.homeAdvantage_tableEdammoCol svg {
  color: var(--themeColorPrimary);
}

.homeAdvantage_tableHeader.homeAdvantage_tableEdammo {
  /*background-color: var(--themeColorPrimary);*/
}

/*
LET
*/

.homeLet {
  display: flex;
  width: 90%;
  margin: auto;
  border: 1px solid black;
  margin-bottom: 4rem;
}

.homeLet_left {
  width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  background-image: url('./res/letbg.webp');
}

.homeLet_leftText {
  max-width: 10rem;
}

.homeLet_leftText > h2 {
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

.homeLet_leftText > p {
  font-size: 0.5rem;
}

.homeLet_right {
  width: 60%;
  display: flex;
}

.homeLet_right > * {
  width: 100%;
}

.homeLet_cardRowL {
  align-self: flex-start;
  margin-bottom: 2rem;
}

.homeLet_cardRowL .homeLet_card {
  margin-left: auto;
  margin-right: 1rem;
}

.homeLet_cardRowR {
  align-self: flex-end;
}

.homeLet_cardRowR .homeLet_card {
  margin-right: auto;
  margin-left: 0.5rem;
}

.homeLet_card {
  width: 60%;
  box-shadow: 0 0 8px 0 rgba(40, 40, 40);
  padding: 0.6rem;
  background-color: var(--themeColorDarkNeg10);
  border-radius: 8px;
  margin: 1rem;
}

.homeLet_cardIcon {
  font-size: 0.7rem;
  color: var(--themeColorPrimary);
}

.homeLet_card > h3 {
  font-size: 0.58rem;
  color: var(--themeColorAccent);
  margin-bottom: 0.3rem;
}

.homeLet_card > p {
  font-size: 0.6rem;
  padding-bottom: 0.6rem;
  border-bottom: 3px solid gray;
  margin-bottom: 0.5rem;
}

/*
CAROUSEL
*/

.homeCarousel {
  max-width: 800px;
  background-image: url('./res/carouselbg.webp');
  margin: auto;
  margin-bottom: 4rem;
}

.homeCarousel_card {
  margin: 1rem auto;
  color: var(--themeColorDark);
  background-color: #acabab;
  padding: 1rem;
}

.homeCarousel_cardWinslow {
  width: 40%;
  border-radius: 10px;
}

.homeCarousel_cardPrakash {
  width: 70%;
  border-radius: 10px;
}

.homeCarousel_card > h3 {
  font-weight: 500;
  width: fit-content;
  margin: auto;
  font-size: 0.9rem;
}

.homeCarousel_cardHr {
  width: 4ch;
  margin: auto;
  height: 3px;
  background-color: var(--themeColorDark);
  margin-bottom: 1rem;
}

.homeCarousel_quote {
  text-align: center;
  font-family: 'Yantramanav', sans-serif;
  font-weight: 200;
  margin-bottom: 0.5rem;
}

.homeCarousel_credit {
  width: fit-content;
  font-size: 0.5rem;
  margin-left: 50%;
}

.homeCarousel .rec-dot {
  margin-top: 0;
  background-color: var(--themeColorDarkNeg10);
  box-shadow: 0 0 0.1rem 0 black;
  border: 1px solid var(--themeColorDarkNeg10);
}

.homeCarousel .rec-dot:hover {
  background-color: var(--themeColorDarkNeg10);
  box-shadow: 0 0 0.1rem 0 black;
  border: border solid var(--themeColorDarkNeg10);
}

.homeCarousel .rec-dot_active {
  background-color: var(--themeColorDarkNeg10);
  box-shadow: 0 0 0.1rem 0 black;
  border: 1px solid white;
}

/*
CONTACT
*/

.homeContact {
  position: relative;
}

.homeContact_nonBg {
  position: absolute;
  height: 13rem; /*THIS HAS TO MATCH .homeContact_particles HEIGHT*/
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url('./res/contactbg.webp');
}

.homeContact_nonBg > h3 {
  max-width: 15ch;
  text-align: center;
  margin-right: 2rem;
  font-size: 1rem;
}

.homeContact_nonBg > form {
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  background-color: var(--themeColorDark);
  border: 2px solid gray;
  width: 15rem;
  height: (inherit - 2rem);
}

.homeContact_nonBg > form > label {
  font-size: 0.5rem;
  margin-bottom: 0.2rem;
  color: #acabab;
  font-weight: bold;
}

.homeContact_nonBg > form > input,
.homeContact_nonBg > form > textarea {
  background-color: inherit;
  border: 2px solid gray;
  border-radius: 4px;
  color: #acabab;
  margin-bottom: 0.7rem;
  font-family: 'Poppins', sans-serif;
  max-width: 100%;
}

.homeContact_nonBg > form > input:focus,
.homeContact_nonBg > form > textarea:focus {
  border-color: skyblue;
  outline: skyblue;
}

.homeContact_nonBg > form > input {
  height: 1rem;
}

.homeContact_nonBg > form > button {
  background-color: var(--themeColorPrimary);
  border: none;
  height: 1.5rem;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
}

.homeContact_nonBg > form > button:hover {
  opacity: 0.9;
  cursor: pointer;
}

.homeContact_particles {
  height: 13rem;
  width: 100%;
  background-color: black;
}
